@import url("https://fonts.googleapis.com/icon?family=Material+Icons");
* {
  padding: 0;
  margin: 0;
  background-repeat: no-repeat;
  scroll-behavior: smooth;
  text-decoration: none;
  position: relative;
}

body,
html {
  margin: 0;
  height: 100%;
  width: 100%;
  background-color: #323E3F;
  overflow-x: hidden;
  z-index: -1;
}

html {
  position: revert;
}

.App {
  text-align: center;
  min-height: 100vh;
  display: grid;
  grid-template-rows: auto 1fr auto;
}
.App .App-header {
  height: 40vh;
  margin: 0 0 1rem;
  color: #ffffff;
  background-color: #0A0A0A;
  overflow: hidden;
  display: grid;
  align-content: center;
}
.App .App-header .App-title {
  font-size: 5em;
  font-weight: bolder;
}
.App .App-header .toolTipContainer {
  margin: 1em auto;
  overflow: hidden;
  max-width: 200px;
}
.App .App-header .toolTipContainer .toolTipSlider {
  white-space: nowrap;
  transition: ease 1000ms;
}
.App .App-header .toolTipContainer .toolTip {
  display: inline-block;
  background-color: #323E3F;
  font-size: 12px;
  width: 100%;
  padding: 10px;
  border-radius: 1em;
  color: #eee;
  white-space: initial;
}
.App .App-header .toolTipContainer .toolTipButtonContainer {
  text-align: center;
}
.App .App-header .toolTipContainer .toolTipButton {
  display: inline-block;
  height: 20px;
  width: 20px;
  border-radius: 100%;
  cursor: pointer;
  margin: 10px 8px 0px;
  background-color: #323E3F;
}
.App .App-header .toolTipContainer .toolTipButton.active {
  background-color: #ffffff;
}
.App .appFooter {
  bottom: 0;
  height: 4em;
  line-height: 4em;
  margin: 1em auto;
  width: 95%;
  color: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.App .appFooter a {
  color: white;
  font-style: italic;
  text-decoration: underline;
}

